/* Dropdowen menu styling */
#schemePageEvent {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
}
#schemePageEvent nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
#schemePageEvent nav ul li {
  float: left;
  width: 100%;
}
#schemePageEvent nav ul li a {
  display: block;
  background: turquoise;
  padding: 10px 15px;
  color: white;
  text-decoration: none;
  transition: 0.2s linear;
}
#schemePageEvent nav ul li ul li a {
  background: #f0f0f0;
  border-left: 4px solid transparent;
  padding: 10px 20px;
  color: #50504f;
}
*,
::after,
::before {
  box-sizing: border-box;
}
#schemePageEvent .sub-menu > a {
  background: white;
  float: left;
  width: 100%;
}
.menuicon {
  display: inline-block;
  margin: 10px 5px;
}
#eventImg {
  width: 30%;
  /* float: left; */
  display: inline-block;
  height: 100%;
}
#schemePageEvent img {
  width: 100%;
  height: 100%;
}
#eventText {
  width: 50%;
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
#schemePageEvent img {
  width: 100%;
  height: 100%;
}
footer {
